.footer {
  padding: 79px 70px 20px;
  max-width: 1280px;
  margin: 0 auto;
}

.footer_hidden {
  visibility: hidden;
  height: 0;
  padding: 0;
  margin: 0;
}

.footer__title {
  margin: 0;
  padding: 0;
  padding-bottom: 20px;
  color: #8B8B8B;
  text-align: center;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-bottom: 1px solid #424242;
}

.navigation {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 20px;
}

.navigation__text {
  margin: 0;
  padding: 0;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .footer {
    padding-top: 60px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 549px) {
  .footer {
    padding-top: 60px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .footer__title {
    font-size: 12px;
  }

  .navigation {
    flex-direction: column-reverse;
    align-items: center;
    padding-top: 28px;
  }

  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding-bottom: 30px;
  }

  .navigation__text {
    font-size: 12px;
  }

  .navigation__text_gray {
    color: #8B8B8B;
  }

  .footer_hidden {
    padding: 0;
  }

}