.card {
  width: 364px;
  height: 271px;
  background: #222;
  box-shadow: 0px 4px 30px 0px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
}

.card__info {
  padding: 14px 14px 0;
  display: grid;
  grid-template-columns: auto 30px;
  grid-template-rows: auto auto;
  grid-template-areas:
    "name button"
    "duration button"
}

.card__link {
  grid-area: poster;
  padding: 0;
  margin: 0;
}

.card__poster {
  margin: 0;
  padding: 0;
  width: 364px;
  height: 203px;
  object-fit: cover;
  object-position: center;
}

.card__name {
  grid-area: name;
  margin: 0;
  padding: 0;
  padding-bottom: 9px;
  max-width: 260px;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card__button {
  grid-area: button;
  margin: 0;
  padding: 0;
  width: 30px;
  height: 30px;
  border: none;
}

.card__button_save {
  background: url(../../images/save-button.svg) transparent no-repeat center;
}

.card__button_save-active {
  background: url(../../images/save-button_active.svg) transparent no-repeat center;
}

.card__button_delete {
  background: url(../../images/delete-button.svg) transparent no-repeat center;
}

.card__duration {
  grid-area: duration;
  margin: 0;
  padding: 0;
  padding-bottom: 14px;
  text-align: start;
  color: #8b8b8b;
  font-size: 11px;
  font-weight: 400;
  line-height: normal;
}

@media screen and (max-width: 768px) {
  .card {
    width: 339px;
    height: 258px;
    padding-bottom: 6px;
  }

  .card__poster {
    width: 339px;
    height: 190px;
  }
}

@media screen and (max-width: 549px) {
  .card {
    width: 300px;
    height: 236px;
  }

  .card__poster {
    width: 300px;
    height: 168px;
  }
}